@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
}
.login-container{margin-top:120px;height:400px;display:flex;flex-direction:column;align-items:center}
.switch{--hue: 220deg;--width: 4rem;--accent-hue: 22deg;--duration: 0.6s;--easing: cubic-bezier(1, 0, 1, 1);--shadow-offset: calc(var(--width) / 20);background-image:linear-gradient(135deg, hsl(var(--hue), 20%, 95%), hsl(var(--hue), 20%, 80%));position:relative;cursor:pointer;display:flex;align-items:center;width:var(--width);height:calc(var(--width)/2.5);border-radius:var(--width);box-shadow:inset 10px 10px 10px hsl(var(--hue), 20%, 80%),inset -10px -10px 10px hsl(var(--hue), 20%, 93%)}.switch .indicator{content:"";position:absolute;width:40%;height:60%;transition:all var(--duration) var(--easing);box-shadow:inset 0 0 2px hsl(var(--hue), 20%, 15%, 60%),inset 0 0 3px 2px hsl(var(--hue), 20%, 15%, 60%),inset 0 0 5px 2px hsl(var(--hue), 20%, 45%, 60%)}.switch .indicator.left{--hue: var(--accent-hue);overflow:hidden;left:10%;border-radius:100px 0 0 100px;background:linear-gradient(180deg, hsl(calc(var(--accent-hue) + 20deg), 95%, 80%) 10%, hsl(calc(var(--accent-hue) + 20deg), 100%, 60%) 30%, hsl(var(--accent-hue), 90%, 50%) 60%, hsl(var(--accent-hue), 90%, 60%) 75%, hsl(var(--accent-hue), 90%, 50%))}.switch .indicator.left::after{content:"";position:absolute;opacity:.6;width:100%;height:100%;background:url("data:image/svg+xml;base64,")}.switch .indicator.right{right:10%;border-radius:0 100px 100px 0;background-image:linear-gradient(180deg, hsl(var(--hue), 20%, 95%), hsl(var(--hue), 20%, 65%) 60%, hsl(var(--hue), 20%, 70%) 70%, hsl(var(--hue), 20%, 65%))}.switch .button{position:absolute;z-index:1;width:55%;height:80%;left:5%;border-radius:100px;background-image:linear-gradient(160deg, hsl(var(--hue), 20%, 95%) 40%, hsl(var(--hue), 20%, 65%) 70%);transition:all var(--duration) var(--easing)}.switch .button::before,.switch .button::after{content:"";position:absolute;top:10%;width:41%;height:80%;border-radius:100%}.switch .button::before{left:5%;box-shadow:inset 1px 1px 2px hsl(var(--hue), 20%, 85%);background-image:linear-gradient(-50deg, hsl(var(--hue), 20%, 95%) 20%, hsl(var(--hue), 20%, 85%) 80%)}.switch .button::after{right:5%;box-shadow:inset 1px 1px 3px hsl(var(--hue), 20%, 70%);background-image:linear-gradient(-50deg, hsl(var(--hue), 20%, 95%) 20%, hsl(var(--hue), 20%, 75%) 80%)}.switch input{display:none}.switch input:checked~.button{left:40%}.switch input:not(:checked)~.indicator.left,.switch input:checked~.indicator.right{box-shadow:inset 0 0 5px hsl(var(--hue), 20%, 15%, 100%),inset 20px 20px 10px hsl(var(--hue), 20%, 15%, 100%),inset 20px 20px 15px hsl(var(--hue), 20%, 45%, 100%)}
.clock{outline:1px dashed #8e44ad;background:#fff;color:#000;display:flex;flex-direction:column;justify-content:center;align-items:center;margin:10px;padding:14px;border:5px solid #8e44ad}
.counter{display:flex;flex-direction:column;justify-content:center;align-items:center;border:1px dashed #ff6b6b;margin:10px;padding:10px}.counter .title{text-align:center;font-weight:bold;color:#4e929d;font-size:18px;margin:0}.counter #value{margin:10px;font-weight:bold;color:#ff6b6b;font-size:36px}.counter .button-container .btn{padding:4px;border:none;margin:2px;font-size:12px;font-weight:bold;min-width:40px;border-radius:4px;background-color:#1a535c;color:#f7fff7}.counter .button-container #reset{background-color:#ff6b6b}
.student-list{width:100%}.student-list .student-card{background-color:#fff;margin:20px 0;padding:20px;color:#8e44ad;display:flex;justify-content:space-between}.student-list .student-card .student-picture{outline:1px solid red}.student-list .student-card .student-info{outline:1px solid blue}
.App{display:flex;flex-direction:column;height:100%;flex:1 1}header{display:flex;align-items:center;justify-content:space-between;font-family:"Montserrat",sans-serif}header #company-name{font-weight:700;font-size:1.2em;text-transform:uppercase}.logo{margin-right:20px}.flex{flex:1 1}.navbar ul{display:flex;margin:0;justify-content:space-evenly}.navbar ul li{list-style-type:none;padding:12px}.navbar ul li a{color:#f6f4e6;text-decoration:none;font-size:1.2em;text-transform:uppercase;font-weight:500;display:inline-block;transition:all .2s ease-in-out}.navbar ul li a:hover{color:#fddb3a}main{color:#f6f4e6;background-color:#aaa;background-image:url("http://localhost:3000/y-so-serious.png");flex:1 1;display:flex;flex-direction:column;justify-content:center;align-items:center}section.clocks{display:flex;justify-content:space-around}section.counters{font-family:"Major Mono Display";display:flex;justify-content:space-around}section.box{background-color:#8e44ad;margin:10px;padding:30px;width:90%;border:1px solid coral}section.box .mentors-container{max-width:1100px;display:flex;justify-content:space-between;align-items:flex-start;flex-wrap:wrap;padding:20px;font-family:"Poppins",sans-serif;color:#000;margin:0px;margin-top:20px;padding:0px;box-sizing:border-box}section.box .mentors-container .profile-card{width:calc(33% - 10px);background:#fff;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:20px 30px;border-radius:5px}section.box .mentors-container .profile-card .image{margin:10px 0;height:150px;width:150px;background:#8e44ad;padding:3px;border-radius:50%}section.box .mentors-container .profile-card .image img{height:100%;width:100%;border-radius:50%;object-fit:cover;border:2px solid #fff}section.box .mentors-container .profile-card p{text-align:justify;margin-top:8px;font-size:16px;font-weight:400}section.box .mentors-container .profile-card .name{margin:10px 0 3px 0;color:#8e44ad;font-size:18px;font-weight:600}section.box .mentors-container .profile-card .btn-row{margin-top:20px;margin-bottom:5px;display:flex;justify-content:space-between;width:100%}section.box .mentors-container .profile-card .btn-row button{background:#8e44ad;width:100%;padding:9px 0px;outline:none;border:2px solid #8e44ad;border-radius:5px;cursor:pointer;font-size:18px;font-weight:400;color:#8e44ad;color:#fff;background:#8e44ad;width:48%}footer{display:flex;justify-content:space-between;align-items:center}.context-count{color:#fff}
